<template>
	<div class="Video">
		<title>Txz</title>
		<Row>
			<Col :xs="{ span: 24, offset: 0 }" :sm="{ span: 24, offset: 0 }" :md="{ span: 24, offset: 0}" :lg="{ span: 24, offset: 0 }">
				<h2 style="margin-top: 10rem">To be continue...</h2>
				<video-player  class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions"></video-player>
			</Col>
		</Row>
    
	</div>
</template>

<script>
export default {

	data()
	{
		return {
			playerOptions: { 
				playbackRates: [0.7, 1.0, 1.5, 2.0], // 
				autoplay: false, //true, 
				muted: false, //  
				loop: false, //  
				preload: 'auto', // <video>auto, 
				language: 'zh-CN', aspectRatio: '16:9', //  - "16:9""4:3" 
				fluid: true, // trueVideo.js player 
				sources: [
				{ 
					type: "video/mp4", 
					src: "https://txz-1256783950.cos.ap-beijing.myqcloud.com/Video/%E7%B2%89%E7%BA%A2%E7%8C%AA%E5%B0%8F%E5%A6%B9%20Peppa%20Pig%20%E7%AC%AC1%E5%AD%A3%E5%86%85%E5%B5%8C%E5%AD%97%E5%B9%95%E7%89%88/S1E01_%E8%B8%8F%E6%B0%B4%E5%9D%91.mp4" //url 
				}], 
				poster: "../../static/images/test.jpg", // 
				// width: document.documentElement.clientWidth, 
				notSupportedMessage: '', //Video.js 
				controlBar: { 
					timeDivider: true, 
					durationDisplay: true, 
					remainingTimeDisplay: false,
					fullscreenToggle: true // 
				} 
			}
		}
	},
	components: {
	},
	methods: {
		onPlayerPlay(player) {
	  		alert("play");
		},
		onPlayerPause(player){
	  		alert("pause");
		},
	},
	computed: {
		player() {
			return this.$refs.videoPlayer.player
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/css" scoped>
  .container {
    background-color: #efefef;
    min-height: 100%;
  }
</style>


